import React from "react";
import { ReactComponent as Chatting } from "../../assets/chatting.svg";
import { ReactComponent as Todo } from "../../assets/to_do_list.svg";
import { ReactComponent as Crown } from "../../assets/shopping.svg";
import { ReactComponent as Message } from "../../assets/message.svg";
import { ReactComponent as Window } from "../../assets/window.svg";
import { ReactComponent as Languages } from "../../assets/languages.svg";
import { ReactComponent as Libraries } from "../../assets/libraries.svg";
import { ReactComponent as Tools } from "../../assets/tools.svg";
import { ReactComponent as Hey } from "../../assets/hey.svg";
import { ReactComponent as Arrow } from "../../assets/arrow.svg";
const SVG = ({ item }) => {
  switch (item) {
    case "chatting":
      return <Chatting />;
    case "todo":
      return <Todo />;
    case "crown":
      return <Crown />;
    case "message":
      return <Message />;
    case "window":
      return <Window />;
    case "languages":
      return <Languages />;
    case "libraries":
      return <Libraries />;
    case "tools":
      return <Tools />;
    case "hey":
      return <Hey />;
    case "arrow":
      return <Arrow />;
    default:
      return <> </>;
  }
};

export default SVG;
