import React from "react";
import { Spring, config } from "react-spring/renderprops";
import SVG from "../svg/svg.component";

import "./home.styles.scss";
const Home = () => {
  return (
    <Spring
      from={{
        opacity: 0,
      }}
      to={{
        opacity: 1,
      }}
      config={config.molasses}
    >
      {(props) => (
        <div style={props} className="home-page">
          <SVG item="hey" />
        </div>
      )}
    </Spring>
  );
};

export default Home;
